.book-loader {
  --color: var(--loader);
  --duration: 6.8s;
  width: 32px;
  height: 12px;
  zoom: 1.5;
  margin: 32px 0 0;
  position: relative;
}

.book-loader .inner {
  width: 32px;
  height: 12px;
  transform-origin: 2px 2px;
  animation: book-loader var(--duration) ease infinite;
  position: relative;
  transform: rotateZ(-90deg);
}

.book-loader .inner .left, .book-loader .inner .right {
  width: 60px;
  height: 4px;
  background: var(--color);
  border-radius: 2px;
  position: absolute;
  top: 0;
}

.book-loader .inner .left:before, .book-loader .inner .right:before {
  content: "";
  width: 48px;
  height: 4px;
  background: inherit;
  border-radius: 2px;
  position: absolute;
  top: -10px;
  left: 6px;
}

.book-loader .inner .left {
  transform-origin: 58px 2px;
  animation: left var(--duration) ease infinite;
  right: 28px;
  transform: rotateZ(90deg);
}

.book-loader .inner .right {
  transform-origin: 2px 2px;
  animation: right var(--duration) ease infinite;
  left: 28px;
  transform: rotateZ(-90deg);
}

.book-loader .inner .middle {
  width: 32px;
  height: 12px;
  border: 4px solid var(--color);
  border-top: 0;
  border-radius: 0 0 9px 9px;
  transform: translateY(2px);
}

.book-loader ul {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 0;
  left: 50%;
}

.book-loader ul li {
  height: 4px;
  transform-origin: 100% 2px;
  width: 48px;
  background: var(--color);
  animation-duration: var(--duration);
  border-radius: 2px;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
  position: absolute;
  top: -10px;
  right: 0;
  transform: rotateZ(0deg)translateX(-18px);
}

.book-loader ul li:nth-child(0) {
  animation-name: page-0;
}

.book-loader ul li:nth-child(1) {
  animation-name: page-1;
}

.book-loader ul li:nth-child(2) {
  animation-name: page-2;
}

