html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

*, :before, :after {
  box-sizing: inherit;
}

.dribbble {
  display: block;
  position: fixed;
  bottom: 24px;
  right: 24px;
}

.dribbble img {
  width: 76px;
  display: block;
}

.hidden {
  display: none;
}

.header {
  width: 100%;
  background: var(--white-cl);
  top: 0;
  left: 0;
}

.header-container {
  border: 1.5px solid var(--black-cl);
  box-shadow: var(--shadow-header);
  border-radius: 0 0 8px 8px;
}

.logo {
  align-items: center;
  gap: 8px;
  display: flex;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(180deg, var(--brand-color) 0%, #dcdcdc 100%);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

input:checked + .slider {
  background: linear-gradient(180deg, var(--brand-color) 0%, #686868 100%);
}

input:focus + .slider {
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.test {
  margin-left: 200px;
  margin-right: auto;
  padding-top: 60px;
  display: block;
}

