  .sign-up-btn {
    background: var(--brand-color);
    border: 1.5px solid var(--black-cl);
    color: var(--white-cl);
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .25));
    border-radius: 18px;
    align-items: center;
    gap: 90px;
    padding: 14px;
    display: flex;
  }

  .switch-burg-wrrap {
    flex-direction: row;
    align-items: center;
    gap: 24px;
    display: flex;
  }

  .switch-btns-wrrap {
